import mixin from '@/common/mixin';
export default {
	data() {
		return {
			refuteMes: {},
			rumorList: [],
			clickId: null,
			showAll: false,
			limit: -1
		};
	},
	mixins: [mixin],
	onLoad() {
			uni.showLoading({
			    title: '加载中'
			});
			this.getStatisticsService();
			this.getIndexRumorList();
			
		
	},
	onPullDownRefresh() {
		this.getStatisticsService();
		this.getIndexRumorList();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	computed: {},
	methods: {
		word(index) {
			if (this.limit == index) {
				return '收起详情∨';
			} else {
				return '展开详情∧';
			}
		},
		getStatisticsService() {
			const self = this;
			uni.request({
				url: self.$baseUrl + '/data/getStatisticsService', //获取首页数据接口。
				data: {},
				header: {},
				success: res => {
					self.refuteMes = res.data;
					uni.hideLoading(); 
					
				},
				fail: () => {
					uni.showToast({
						title: '网络请求失败！',
						duration: 1500
					});
				}
			});
		},
		getIndexRumorList() {
			const self = this;
			uni.request({
				url: self.$baseUrl + '/data/getIndexRumorList', //获取首页数据接口。
				data: {},
				header: {},
				success: res => {
					self.rumorList = res.data;
				},
				fail: () => {
					uni.showToast({
						title: '网络请求失败！',
						duration: 1500
					});
				}
			});
		},
		showInfo(index) {
			if (index == this.limit) {
				this.limit = -1;
			} else {
				this.limit = index;
			}

			// if (this.$refs[index][0].childNodes[1].style.display == 'none') {
			// 	this.$refs[index][0].childNodes[1].style.display = 'block';
			// } else if (this.$refs[index][0].childNodes[1].style.display == 'block') {
			// 	this.$refs[index][0].childNodes[1].style.display = 'none';
			// }
		}
	}
};
